a,
a:focus,
a:hover {
  color: #fff;
}

html,
body {
  background-color: #333;
}

body {
  display: flex;

  width: 100%;
  min-height: 100vh;

  box-shadow: inset 0 0 5rem rgba(0, 0, 0, .5);
}

.container {
  max-width: 42em;
}

#start-box .search-input:disabled {
  background-color: #aaa;
}

#start-box fieldset:disabled .non-selected-wrapper {
  background-color: #888;
}

#start-box fieldset:disabled .selected-wrapper {
  background-color: #bbb;
}

#loading-box {
  display: none;
}

#activity-select-box {
  display: none;
}
